.header {
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 60px;
	padding: 0;
	background: #4770bf;
	box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
	z-index: 50;
}

.header h1 {
	float: left;
	margin: 0;
	padding: 0 15px;
	font-size: 26px;
	line-height: 56px;
	font-weight: 400;
	color: #FFF;
	font-weight:bold;
}

.header nav {
	float: right;
	font-size: 100%;
}

.header nav a {
	display: inline-block;
	height: 56px;
	line-height: 56px;
	padding: 0 15px;
	min-width: 50px;
	text-align: center;
	background: rgba(255,255,255,0);
	text-decoration: none;
	color: #FFF;
	will-change: background-color;
}

.header nav a:hover,
.header nav a:active {
	background: rgba(0,0,0,0.2);
}

.header nav a.active {
	background: rgba(0,0,0,0.4);
}

code[class*=language-],pre[class*=language-] {
    word-wrap: normal;
    color: var(--color-code-text);
    hyphens: none;
    line-height: 1.5;
    tab-size: 4;
    text-align: left;
    white-space: pre;
    word-break: normal;
    word-spacing: normal
}

pre[class*=language-] {
    margin: .5em 0;
    overflow: auto;
    padding: 1em
}

:not(pre)>code[class*=language-],pre[class*=language-] {
    background: #2d2d2d
}

:not(pre)>code[class*=language-] {
    border-radius: .3em;
    padding: .1em;
    white-space: normal
}

.token.block-comment,.token.cdata,.token.comment,.token.doctype,.token.prolog {
    color: var(--color-code-comment)
}

.token.punctuation {
    color: var(--color-code-punctuation)
}

.token.attr-name,.token.deleted,.token.namespace,.token.tag {
    color: var(--color-code-tag)
}

.token.interpolation-punctuation {
    color: var(--color-code-interpolation-punctuation)
}

.token.function-name {
    color: var(--color-code-function-name)
}

.token.boolean,.token.function,.token.number {
    color: var(--color-code-function)
}

.token.class-name,.token.constant,.token.property,.token.symbol {
    color: var(--color-code-symbol)
}

.token.atrule,.token.builtin,.token.important,.token.keyword,.token.selector {
    color: var(--color-code-keyword)
}

.token.attr-value,.token.char,.token.regex,.token.string,.token.variable {
    color: var(--color-code-string)
}

.token.entity,.token.operator,.token.url {
    color: var(--color-code-operator)
}

.token.bold,.token.important {
    font-weight: 700
}

.token.italic {
    font-style: italic
}

.token.entity {
    cursor: help
}

.token.inserted {
    color: green
}

pre[data-line] {
    padding: 1em 0 1em 3em;
    position: relative
}

.line-highlight {
    background: hsla(24,20%,50%,.08);
    background: linear-gradient(90deg,hsla(24,20%,50%,.1) 70%,hsla(24,20%,50%,0));
    left: 0;
    line-height: inherit;
    margin-top: 1em;
    padding-bottom: inherit;
    padding-left: 0;
    padding-right: 0;
    padding-top: inherit;
    pointer-events: none;
    position: absolute;
    right: 0;
    white-space: pre
}

.line-highlight:before,.line-highlight[data-end]:after {
    background-color: hsla(24,20%,50%,.4);
    border-radius: 999px;
    box-shadow: 0 1px #fff;
    color: #f5f2f0;
    content: attr(data-start);
    font: 700 65%/1.5 sans-serif;
    left: .6em;
    min-width: 1em;
    padding: 0 .5em;
    position: absolute;
    text-align: center;
    text-shadow: none;
    top: .4em;
    vertical-align: .3em
}

.line-highlight[data-end]:after {
    bottom: .4em;
    content: attr(data-end);
    top: auto
}

.line-numbers .line-highlight:after,.line-numbers .line-highlight:before {
    content: none
}

ol,ul {
    margin-left: 0
}

select {
    font-size: 100%
}

.btn {
    border: .2rem solid var(--color-btn-background);
    border-radius: .25rem;
    box-shadow: 0 .125rem 0 rgba(var(--color-brand),.3);
    display: inline-block;
    letter-spacing: .075em;
    margin: 0 1rem;
    min-width: 13em;
    padding: .5rem 1.5rem;
    text-decoration: none!important;
    text-transform: uppercase;
    white-space: nowrap
}

@media (min-width:600px) {
    .btn {
        padding: .75rem 2rem
    }}

.btn.primary {
    background: var(--color-btn-background)!important;
    color: #000!important
}

.btn.secondary {
    background: transparent!important;
    color: var(--color-btn-secondary)!important
}

.intro-buttons{
	-ms-flex-align:center;align-items:center;display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;margin-bottom:3rem
	}

.intro-buttons .btn.secondary {
    color: #fff!important
}

.btn+.btn {
    margin-top: 1.3rem
}

@media (min-width:600px) {
    .btn+.btn {
        margin-top: 0
    }}

.btn-small {
    background: var(--color-brand);
    border: .2rem solid var(--color-brand);
    border-radius: .25rem;
    box-shadow: 0 .125rem 0 rgba(var(--color-brand),.3);
    color: #fff;
    display: inline-block;
    font-size: 1rem;
    padding: .25rem 1rem;
    text-decoration: none;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    transition: background-color .3s,border-color .3s,color .3s;
    white-space: nowrap
}

.btn-small:active {
    -webkit-transform: translate3d(0,5%,0);
    transform: translate3d(0,5%,0)
}

.btn-small:hover {
    background: #fff;
    border-color: #fff;
    color: #000
}

.intro-buttons{
	-ms-flex-align:center;align-items:center;display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;margin-bottom:3rem
	}
	
@media (min-width:600px)
{
	main .markup .intro-buttons{-ms-flex-pack:center;-ms-flex-align:initial;align-items:normal;-ms-flex-direction:row-reverse;flex-direction:row-reverse;justify-content:center;margin-bottom:4.5rem}
	
}

