body,html 
{
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: var(--color-page-bg);
    color: var(--color-text);
    font: 16px/1.65 system-ui,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,Fira Sans,Droid Sans,Helvetica Neue,sans-serif;
    font-weight: 400;
    margin: 0;
    padding: 0
}

.tagline {
	color:#000011;
	display:block;
	font-size:1.2rem;
	font-weight:300;
	line-height:0.2;
	margin:1rem 0 1.5rem;
}

.btn {
    border: .2rem solid var(--color-btn-background);
    border-radius: .25rem;
    box-shadow: 0 .125rem 0 rgba(var(--color-brand),.3);
    display: inline-block;
    letter-spacing: .075em;
    margin: 0 1rem;
    min-width: 13em;
    padding: .5rem 1.5rem;
    text-decoration: none!important;
    text-transform: uppercase;
    white-space: nowrap
}

@media (min-width:600px) {
    .btn {
        padding: .75rem 2rem
    }}

.btn.primary {
    background: var(--color-btn-background)!important;
    color: #000!important
}

.btn.secondary {
    background: transparent!important;
    color: var(--color-btn-secondary)!important
}

.intro-buttons .btn.secondary {
    color: #fff!important
}

.btn+.btn {
    margin-top: 1.3rem
}

@media (min-width:600px) {
    .btn+.btn {
        margin-top: 0
    }}

.btn-small {
    background: var(--color-brand);
    border: .2rem solid var(--color-brand);
    border-radius: .25rem;
    box-shadow: 0 .125rem 0 rgba(var(--color-brand),.3);
    color: #fff;
    display: inline-block;
    font-size: 1rem;
    padding: .25rem 1rem;
    text-decoration: none;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    transition: background-color .3s,border-color .3s,color .3s;
    white-space: nowrap
}

.btn-small:active {
    -webkit-transform: translate3d(0,5%,0);
    transform: translate3d(0,5%,0)
}

.btn-small:hover {
    background: #fff;
    border-color: #fff;
    color: #000
}

.intro-buttons{
	-ms-flex-align:center;align-items:center;display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;margin-bottom:3rem
	}

h1
{
	font-weight:bold;
}

* {
	box-sizing: border-box;
}

.intro-buttons{
	-ms-flex-align:center;align-items:center;display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;margin-bottom:3rem
	}
	
